h2{
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: #2f0c0c;
    font-size: 30px;

}

h3{
    font-family: 'Raleway', sans-serif;
    font-size: 20px;
}

.titulo{
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: #2f0c0c;
    text-align: center;
    margin-top: 30px;
    
}

.div-missao{
    background-color: #c5abae;
    margin-top: 30px;
    height: 200px;  
}

.h2Subtitle{
   padding-top: 30px;
   padding-left: 10px;
}

.texto{
    padding-left: 20px;
    color: #2f0c0c;
}

@media (max-width: 990px)
{
    
    .titulo{
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        color: #2f0c0c;
        text-align: center;
        margin-top: 5px;
        font-size: 30px;
        
    }

    h2{
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        color: #2f0c0c;
        font-size: 20px;
    
    }
    
    h3{
        font-family: 'Raleway', sans-serif;
        font-size: 12px;
    }
    
    
    .div-missao{
        background-color: #c5abae;
        margin-top: 5px;
        height: 200px;  
        
    }
    
    .h2Subtitle{
       padding-top: 5px;
       padding-left: 10px;
    }
    
    .texto{
        padding-left: 20px;
        color: #2f0c0c;
        margin-bottom: 20px;
        width: 300px;
    
        
    }
  
}
